@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


input[type="email"],
input[type="text"],
input[type="password"],
input[type="number"],
textarea,
select {
  width: 100% !important;
}

label {
  margin-top: 1rem;
}

label.checkbox {
  font-weight: normal;
}

button[type="submit"] {
  margin-top: 1rem;
}

.login-btn {
  background-color: forestgreen;
  display:inline-block;
  padding:0.3em 1.2em;
  border:0.16em solid rgba(255,255,255,0);
  border-radius:2em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:#FFFFFF;
  text-shadow: 0 0.04em 0.04em rgba(0,0,0,0.35);
  text-align:center;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.login-btn:hover{
  border-color: rgba(255,255,255,1);
}

.login-btn:focus{
  outline: none;
  box-shadow: none;
}

@media all and (max-width:30em) {
  .login-btn {
    display: block;
    margin: 0.2em auto;
  }
}
.switch-btn:focus {
    box-shadow: none;
}
.dropdown-nav-button {
    margin-top: 1.5px;
}

#dropdown-basic-button {
    border: 1px solid transparent;
    color: white;
    text-decoration: none;
    padding: 0px;
    font-family: Arial, Helvetica, sans-serif;
}

#dropdown-basic-button:hover {
    color: lawngreen;
}

#dropdown-basic-button:focus {
    outline: none;
    border-color: black;
    box-shadow: none;
}

.nav-route {
    border: 1px solid transparent;
    color: white;
    text-decoration: none;
    padding: 8px;
    font-family: Arial, Helvetica, sans-serif;
}

.nav-route:hover,
.nav-route:active,
.nav-route.active {
    background: black;
    color: lawngreen;
    text-decoration: none;
}

.nav-route button {
    cursor: pointer;
    border: 1px solid #292929;
    color: #292929;
    background: transparent;
    padding: 0.5rem;
    font: inherit;
}

.nav-route button:focus {
    outline: none;
}

.nav-route button:hover,
.nav-route button:active {
    background: #292929;
    color: white;
}

.login-signup-btn {
    outline: none;
    border-color: lawngreen;
    background-color: lawngreen;
    margin-top: 5px;
    height: 35px;
}

@media (min-width: 768px) {
    .nav-route {
        flex-direction: row;
    }
    
    .nav-route li {
        margin: 0 0.5rem;
    }
    
    .nav-route a {
        color: white;
        text-decoration: none;
    }
    
    .nav-route button {
        border: 1px solid white;
        color: white;
        background: transparent;
    }
        
    .nav-route button:hover,
    .nav-route button:active {
        background: black;
        color: #292929;
        }
}

